.progress {
  height: 40px;
  position: relative;
  top: 45px;
  left: 50px;
  margin: -20px 0 0 -50px;
  display: none;
}

.progress.show {
  display: block;
}

.progress span {
  transition: all 500ms ease;
  height: 10px;
  width: 10px;
  display: inline-block;
  border-radius: 10px;
  animation: wave 1s ease infinite;
}

.progress span:nth-child(1) {
  animation-delay: 0;
}

.progress span:nth-child(2) {
  animation-delay: 100ms;
}

.progress span:nth-child(3) {
  animation-delay: 200ms;
}

.progress span:nth-child(4) {
  animation-delay: 300ms;
}

.progress span:nth-child(5) {
  animation-delay: 400ms;
}

.progress span:nth-child(6) {
  animation-delay: 500ms;
}

.progress span:nth-child(7) {
  animation-delay: 600ms;
}

.progress span:nth-child(8) {
  animation-delay: 700ms;
}

@keyframes wave {
  0%, 40%, 100% {
    transform: translate(0, 0);
    background-color: #c1ff72;
  }
  10% {
    transform: translate(0, -15px);
    background-color: #c1ff72;
  }
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
